<template>
    <div class="result">
        <div class="top">
            <div id="canvas">
                <img class="title" src="../images/zsd.png">
                <div class="center" :style="{backgroundColor: rgba}">
                    <img class="cn" src="../images/f-day_img_resultcard_pic-boy2.png">
                    <div class="miaoshu">
                        <p class="name">{{name}}</p>
                        <div class="fenshu-outer">:<span class="fenshu">{{score}}</span></div>
                        <div>:<p class="dw">{{dw}}</p></div>
                    </div>
                </div>
                <div class="by">{{by}}</div>
                <div class="wa">{{wa}}</div>
                <div class="bottom-desc">
                    <img  class='left' src="../images/f-day_img_openning-logo.png"/>
                    <img  class="right" src="../images/qrcode_wechat.png"/>
                    <span  class="sentance" ></span>
                </div>
            </div>
        </div>
        <p class="c">**</p>
        <Button @click.native="downLoadPage" class="button">CALL</Button>
    </div>
</template>

<script>
    import Button from '@/components/button.vue'
    import html2canvas from 'html2canvas'
    import shareTool from '@/components/shareandgetname.js'

    function randomNum(minNum,maxNum){
        switch(arguments.length){
            case 1:
                return parseInt(Math.random()*minNum+1,10);
                break;
            case 2:
                return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
                break;
            default:
                return 0;
                break;
        }
    }



    export default {
        name: "result",
        data(){
          return {
              name:localStorage.getItem('name'),
              score:localStorage.getItem('score'),
              sex: localStorage.getItem('sex'),
              dw: 'III',
              by:'',
              wa:'',
              rgba:'rgba(0,0,0,0.5)'
          }
        },
        methods:{
            downLoadPage(){
                this.$router.push({path: '/downloadpage'})
            },
            changeToimg(){
                const dom = document.querySelector('#canvas')
                html2canvas(dom, {onclone:(document)=>{
                    document.querySelector('#canvas').style.visibility = 'initial'
                    }}).then((canvas)=>{
                        const img = new Image()
                        img.src = canvas.toDataURL()
                        img.id = 'result-img'
                        img.style.height = '100%'
                        dom.innerHTML = ''
                        dom.appendChild(img)
                        dom.style.visibility = 'initial'
                        //document.querySelector('.top').innerHTML = ''
                        //document.querySelector('.top').appendChild(img)
                })
            },
            getdw(){
                switch(true){
                    case this.score>=0 && this.score <=1: this.dw = 'III';break;
                    case this.score>=2 && this.score <=4: this.dw = 'II';break;
                    case this.score>=5 && this.score <=7: this.dw = 'I';break;
                    case this.score>=8 && this.score <=12: this.dw = 'III';break;
                    case this.score>=13 && this.score <=17: this.dw = 'II';break;
                    case this.score>=18 && this.score <=20: this.dw = 'I';break;
                    case this.score>=21 && this.score <=25: this.dw = 'IV';break;
                    case this.score>=26 && this.score <=30: this.dw = 'III';break;
                    case this.score>=31 && this.score <=35: this.dw = 'II';break;
                    case this.score>=36 && this.score <=40: this.dw = 'I';break;
                    case this.score>=41 && this.score <=45: this.dw = 'IV';break;
                    case this.score>=46 && this.score <=50: this.dw = 'III';break;
                    case this.score>=51 && this.score <=55: this.dw = 'II';break;
                    case this.score>=56 && this.score <=60: this.dw = 'I';break;
                    case this.score>=61 && this.score <=66: this.dw = 'V';break;
                    case this.score>=67 && this.score <=72: this.dw = 'IV';break;
                    case this.score>=73 && this.score <=78: this.dw = 'III';break;
                    case this.score>=79 && this.score <=84: this.dw = 'II';break;
                    case this.score>=85 && this.score <=89: this.dw = 'I';break;
                    case this.score>=90 && this.score <=90: this.dw = '';break;
                    case this.score>=97 && this.score <=100: this.dw = '';break;
                }
            },
            getRGBA(){
                switch(true){
                    case this.score<=20:this.rgba =  'rgba(44,140,67,0.5)';break;
                    case this.score<=40:this.rgba =  'rgba(149,170,59,0.5)';break;
                    case this.score<=60:this.rgba =  'rgba(251,172,33,0.5)';break;
                    case this.score<=80:this.rgba =  'rgba(240,77,60,0.5)';break;
                    case this.score<=100:this.rgba = 'rgba(238,53,96,0.7)';break;
                }
            },
            getWA(){
                if(this.sex == 'woman'){//randomNum
                    switch(true){
                        case this.score>=0 && this.score<=7:   this.by = ['','',''][randomNum(0,2)];this.wa = ['', ''][randomNum(0,1)];break;
                        case this.score>=8 && this.score<=20:  this.by =['', '', ''][randomNum(0,2)];this.wa = ['', ''][randomNum(0,1)];break;
                        case this.score>=21 && this.score<=40: this.by = ['','',''][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=41 && this.score<=60: this.by = ['', '',''][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=61 && this.score<=80: this.by = ['','', ''][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=81 && this.score<=95: this.by = ['!','' , 'No Keng Dad No Die'][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=96 && this.score<=100:this.by = ['' , '','FUCK'][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                    }
                }else{

                    switch(true){
                        case this.score>=0 && this.score<=7:    this.by = ['','',''][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=8 && this.score<=20:   this.by = ['','',''][randomNum(0,2)];this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=21 && this.score<=40:  this.by = ['','', ''][randomNum(0,2)];   this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=41 && this.score<=60:  this.by = ['' , '' , ''][randomNum(0,2)]; this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=61 && this.score<=80:  this.by = [',' , '' , ''][randomNum(0,2)]; this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=81 && this.score<=95:  this.by = ['' , '' , ''][randomNum(0,2)]; this.wa = ['',''][randomNum(0,1)];break;
                        case this.score>=96 && this.score<=100: this.by = ['','', ''][randomNum(0,2)]; this.wa = ['',''][randomNum(0,1)];break;
                    }
                }
            }
        },
        components:{
            Button
        },
        mounted(){
            this.getWA()
            this.getRGBA()
            this.getdw()

            const title = ''+ this.name + '' + this.dw +''

            const share = new shareTool({pic:'https://webres.psy-1.com/sgy/dist/static/img/f-day_img_openning-daddy@3x.1695a13.png', url:window.location.href.split('#')[0], title:title, desc:''})
            if(this.$store.getters.isApp)  share.appInit()
            if(this.$store.getters.isWeiXin)share.weiXinShare()
            this.changeToimg()
        }
    }
</script>

